
Template.body.addContent((function() {
  var view = this;
  return "";
}));
Meteor.startup(Template.body.renderToDocument);

Template.__checkName("ApplicationLayout");
Template["ApplicationLayout"] = new Template("Template.ApplicationLayout", (function() {
  var view = this;
  return [ Blaze._TemplateWith(function() {
    return "navbar";
  }, function() {
    return Spacebars.include(view.lookupTemplate("yield"));
  }), "\n	  ", HTML.DIV({
    "class": "container"
  }, "\n		", Blaze._TemplateWith(function() {
    return "website_form";
  }, function() {
    return Spacebars.include(view.lookupTemplate("yield"));
  }), "\n		", Blaze._TemplateWith(function() {
    return "main";
  }, function() {
    return Spacebars.include(view.lookupTemplate("yield"));
  }), "\n		", Blaze._TemplateWith(function() {
    return "comments";
  }, function() {
    return Spacebars.include(view.lookupTemplate("yield"));
  }), "\n		", Spacebars.include(view.lookupTemplate("modal")), "\n	  ") ];
}));

Template.__checkName("modal");
Template["modal"] = new Template("Template.modal", (function() {
  var view = this;
  return HTML.Raw('<div class="modal fade" id="modal-dialog" role="dialog">\n		<div class="modal-dialog">\n		  <div class="modal-content">\n			<div class="modal-header">\n			  <button type="button" class="close" data-dismiss="modal">&times;</button>\n			</div>\n			<div class="modal-body" id="dialog-body">\n			  You must be login first.\n			</div>\n		  </div>\n		</div>\n	  </div>');
}));

Template.__checkName("navbar");
Template["navbar"] = new Template("Template.navbar", (function() {
  var view = this;
  return HTML.NAV({
    "class": "navbar navbar-default"
  }, HTML.Raw('\n	  <a class="navbar-brand" href="/">Social Website Aggregator</a>\n		'), HTML.DIV({
    "class": "container-fluid"
  }, "\n		  ", HTML.Raw('<div class="navbar-header">\n		  </div>'), "\n		  ", HTML.FORM({
    "class": "navbar-form navbar-right js-search-form"
  }, "\n			", Spacebars.include(view.lookupTemplate("loginButtons")), "	\n			", HTML.Raw('<input type="text" id="search">'), "\n			", HTML.Raw('<button type="submit">Search</button>'), "\n		  "), "\n		"), "\n	  ");
}));

Template.__checkName("website_form");
Template["website_form"] = new Template("Template.website_form", (function() {
  var view = this;
  return HTML.Raw('<a class="btn btn-default js-toggle-website-form" href="#">\n		  <span><strong>Add new</strong></span>\n	  </a>\n	  <div id="website_form" class="hidden_div">\n		<form class="js-save-website-form">\n		  <div class="form-group">\n			<label for="url">Site address</label>\n			<input type="text" class="form-control" id="url" placeholder="http://www.mysite.com">\n		  </div>\n		  <div class="form-group">\n			<label for="title">Title</label>\n			<input type="text" class="form-control" id="title" placeholder="Mysite">\n		  </div>\n		  <div class="form-group">\n			<label for="description">Description</label>\n			<input type="text" class="form-control" id="description" placeholder="This site seems very useful for ...">\n		  </div>\n\n		  <button type="submit" class="glyphicon glyphicon-send"></button>\n		</form>\n	  </div>');
}));

Template.__checkName("website_list");
Template["website_list"] = new Template("Template.website_list", (function() {
  var view = this;
  return HTML.DIV("\n	  ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("websites"));
  }, function() {
    return [ "\n		", HTML.DIV({
      "class": "card"
    }, "\n		  ", HTML.SPAN({
      "class": "rank"
    }, Blaze.View("lookup:@index", function() {
      return Spacebars.mustache(view.lookup("@index"));
    })), "\n		  ", HTML.DIV({
      "class": "column-middle"
    }, "\n			", HTML.A({
      href: "#",
      "class": "btn btn-success js-upvote"
    }, "\n			  ", HTML.SPAN({
      "class": "glyphicon glyphicon-hand-up",
      "aria-hidden": "true"
    }), "\n			"), "\n			", HTML.SPAN(Blaze.If(function() {
      return Spacebars.call(view.lookup("rating"));
    }, function() {
      return Blaze.View("lookup:rating", function() {
        return Spacebars.mustache(view.lookup("rating"));
      });
    }, function() {
      return "0";
    })), "\n			", HTML.A({
      href: "#",
      "class": "btn btn-danger js-downvote"
    }, "\n			  ", HTML.SPAN({
      "class": "glyphicon glyphicon-hand-down",
      "aria-hidden": "true"
    }), "\n			"), "\n		  "), "\n		  ", HTML.DIV({
      "class": "text-left"
    }, "\n			", HTML.P(HTML.A({
      href: function() {
        return [ "/site/", Spacebars.mustache(view.lookup("_id")) ];
      }
    }, Blaze.View("lookup:title", function() {
      return Spacebars.mustache(view.lookup("title"));
    })), " ", HTML.A({
      href: function() {
        return Spacebars.mustache(view.lookup("url"));
      }
    }, HTML.CharRef({
      html: "&gt;",
      str: ">"
    }), HTML.CharRef({
      html: "&gt;",
      str: ">"
    }))), "\n			", HTML.P(Blaze.View("lookup:description", function() {
      return Spacebars.mustache(view.lookup("description"));
    })), "\n			", HTML.P({
      "class": "tagline"
    }, "sent ", Blaze.View("lookup:formatDate", function() {
      return Spacebars.mustache(view.lookup("formatDate"), view.lookup("createdOn"));
    }), " by ", Blaze.If(function() {
      return Spacebars.call(view.lookup("createdBy"));
    }, function() {
      return Blaze.View("lookup:createdBy", function() {
        return Spacebars.mustache(view.lookup("createdBy"));
      });
    }, function() {
      return "not registered";
    })), "\n		  "), "\n		"), "\n	  " ];
  }), "\n	  ");
}));

Template.__checkName("website_detail");
Template["website_detail"] = new Template("Template.website_detail", (function() {
  var view = this;
  return [ HTML.H2(HTML.A({
    href: function() {
      return Spacebars.mustache(view.lookup("url"));
    }
  }, Blaze.View("lookup:title", function() {
    return Spacebars.mustache(view.lookup("title"));
  }))), "\n	  ", HTML.DIV({
    "class": "site-detail"
  }, "\n		", HTML.P({
    "class": "tagline"
  }, "sent ", Blaze.View("lookup:formatDate", function() {
    return Spacebars.mustache(view.lookup("formatDate"), view.lookup("createdOn"));
  }), " by ", Blaze.If(function() {
    return Spacebars.call(view.lookup("createdBy"));
  }, function() {
    return Blaze.View("lookup:createdBy", function() {
      return Spacebars.mustache(view.lookup("createdBy"));
    });
  }, function() {
    return "not registered";
  })), "\n		", HTML.P(Blaze.View("lookup:description", function() {
    return Spacebars.mustache(view.lookup("description"));
  })), "\n	  "), "\n	  ", HTML.DIV({
    id: "comment-form",
    "class": "card"
  }, "\n		", HTML.Raw("<h3>Post your comment...</h3>"), "\n		", HTML.FORM({
    "class": "js-save-comment-form"
  }, "\n		  ", HTML.DIV({
    "class": "form-group"
  }, "\n			", HTML.TEXTAREA({
    id: "comment",
    "class": "form-control",
    placeholder: "..here."
  }), "\n		  "), "\n		  ", HTML.Raw('<button type="submit" class="glyphicon glyphicon-send"></button>'), "\n		"), "\n	  ") ];
}));

Template.__checkName("website_comments");
Template["website_comments"] = new Template("Template.website_comments", (function() {
  var view = this;
  return HTML.DIV({
    "class": "comments card"
  }, HTML.Raw("\n		<h3>Comments:</h3>\n		"), Blaze.Each(function() {
    return Spacebars.call(view.lookup("comments"));
  }, function() {
    return [ "\n		", HTML.DIV({
      "class": "comment"
    }, "\n		  ", HTML.P({
      "class": "tagline"
    }, "sent ", Blaze.View("lookup:formatDate", function() {
      return Spacebars.mustache(view.lookup("formatDate"), view.lookup("createdOn"));
    }), " by ", Blaze.If(function() {
      return Spacebars.call(view.lookup("createdBy"));
    }, function() {
      return Blaze.View("lookup:createdBy", function() {
        return Spacebars.mustache(view.lookup("createdBy"));
      });
    }, function() {
      return "not registered";
    })), "\n		  ", HTML.P(Blaze.View("lookup:comment", function() {
      return Spacebars.mustache(view.lookup("comment"));
    })), "\n		"), "\n		" ];
  }), "\n	  ");
}));
